import { Input, InputGroup, InputLeftElement, Button, ButtonGroup } from '@chakra-ui/react'
import { SearchIcon, ArrowDownIcon, ArrowUpIcon } from '@chakra-ui/icons'
import { useCanvasContext } from '@components/Canvas/hooks'
import React from 'react'

function LayerPanel() {
  const { canvas, activeObject } = useCanvasContext()
  const [count, setCount] = React.useState(1)
  console.log('render LayerPanel')
  return (
    <>
      <div style={{ padding: '1rem 2rem' }}>Layers</div>
      <div style={{ padding: '1rem 2rem' }}>
        {canvas.getObjects().map((v: any, i) => {
          return (
            <div key={i} style={{ background: 'white', marginBottom: 5, padding: 5 }}>
              <div>
                {i} - {v.id || 'No ID'} - {v.type || 'No Type'}
              </div>
              <div>
                <ButtonGroup size="sm" isAttached variant="outline">
                  <Button
                    size="sm"
                    onClick={() => {
                      canvas.sendBackwards(v)
                      canvas.renderAll()
                      setCount(count + 1)
                    }}
                  >
                    <ArrowUpIcon />
                  </Button>
                  <Button
                    size="sm"
                    onClick={() => {
                      // v.bringToFront()//up top
                      v.bringForward()
                      canvas.renderAll()
                      setCount(count + 1)
                    }}
                  >
                    <ArrowDownIcon />
                  </Button>
                </ButtonGroup>
                <Button
                  onClick={() => {
                    v.selectable = !v.selectable
                    canvas.renderAll()
                    setCount(count + 1)
                  }}
                >
                  {v.selectable == true ? 'Selectable' : 'NoSelectable'}
                </Button>
                <Button
                  onClick={() => {
                    if (v.evented === false) {
                      delete v.evented
                    } else {
                      v.evented = false
                    }
                    canvas.renderAll()
                    setCount(count + 1)
                  }}
                >
                  {v.evented === false ? 'DisEvented' : 'Evented'}
                </Button>
              </div>
            </div>
          )
        })}
      </div>
    </>
  )
}
export default LayerPanel
